.nft-container {
    border-radius: 0.6rem;
    overflow: hidden;
    margin: 1rem;
    width: 9rem;
}
.social-glow {
    position: relative;
    display: inline-block;
    width: 3rem;
    height: 3rem;
    text-align: center;
    line-height: 3rem;
    font-size: 30px;
    color: rgb(255, 255, 255);
    transition: .5s;
    /* margin-left: 0.8rem;
    margin-right: 0.8rem; */
    /* background: rgba(0, 0, 0, 0.267); */
    /* border-radius: 50%; */
}

.social-glow::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20%;
    transition: .5s;
    transform: scale(.9);
    z-index: -1;
}

.socials-button-container{
    position: fixed;
    left: 1.5rem;
    bottom: 1rem;
    z-index: 1;
}

.social-glow:hover::before {
    transform: scale(1.1);
    box-shadow: 0 0 15px #6410ff;
}

.social-glow:hover {
    color: #ffffff;
}

#pincubatorNFT{
    cursor: pointer !important;
}

.control-container {
    cursor: pointer;
    height: 1.2rem;
    width: 1.2rem;
    overflow: hidden;
}

.control-wrapper{
    position: relative;
    height: 0;
    z-index: 2;
    bottom: 2rem;
    left: 0.5rem;
}
.control-container:last-child {
    margin: 0;
}
.note {
    width: 100%;
    height: 100%;
    background-size: cover;
    -webkit-clip-path: url(#my-clip-path);
    clip-path: url(#my-clip-path);
    height: 100%;
    width: 100%;
}

.fill {
    height: 240%;
    transform: translateY(-58%);
    transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
    width: 100%;
}
.fill1 {
    background: linear-gradient(
        #ffffff34 0%,
        #ffffff3f 42%,
        #ffffff 60%,
        #ffffff 100%
    );
}

.svg {
    position: absolute;
    width: 0;
    height: 0;
}


.strike-through {
    clip-path: polygon(0% 0%, -28% 99%, -31% 116%, 20% 0%);
    position: absolute;
    /* top: 0; */
    transition: clip-path 300ms cubic-bezier(0.4, 0, 0.2, 1);
    /* width: 100%; */
    width: 1.4rem;
    bottom: -1.3rem;
    left: 0.06rem;
}

.control-container.mute .strike-through {
    clip-path: polygon(0% 0%, -28% 99%, 84% 137%, 122% 10%);
}
.control-container.mute .fill {
    transform: translateY(0);
}

